export function asString(value) {
  if (typeof value === 'string') {
    return value;
  }

  return '';
}

export function fullName(account) {
  let name = '';

  name += asString(account.FirstName) + ' ';
  if (asString(account.FamilyNamePrefix)) {
    name += asString(account.FamilyNamePrefix) + ' ';
  }
  name += asString(account.FamilyName);

  return name;
}

export function organisationName(businessPartner) {
  return businessPartner.Organisation ? businessPartner.Organisation.OrganisationName : ''
}