export default {
  props: {
    config: Object
  },
  template: '<div><loading-overlay v-if="loading"></loading-overlay><router-view :config="config"></router-view></div>',
  data: function () {
    return {
      loading: false
    }
  },
  provide: function () {
    return {
      loader: {
        start: () => {
          this.loading = true;
        },
        stop: () => {
          this.loading = false;
        }
      }
    }
  },
}