import Vue from "vue";
import VueComponents from '@nn/vue-components';
import { store } from './store/index';
import createRouter from './router/index';
import Index from './pages/index';
import { createVueChildContainer } from '../app-vue-shared/createVueChildContainer';

// Shared componenents (logic independent and can be used anywhere in any Vue project
import '../app-vue-shared/shared.js';
// Common component, has logic and is specific for a project but shared between pages
import './common';

Vue.use(VueComponents);

const bootstrapAccountManagement = (root, config) => {
  new Vue({
    el: createVueChildContainer(root),
    store,
    router: createRouter(),
    render: h =>
      h(Index, { props: { config } })
  });
}

export default bootstrapAccountManagement;