<template>
<div>
  <h1>Admin account Mijn NN Zakelijk aanmaken</h1>
  <account-steps current="1"></account-steps>
  <div class="o-layout">
    <div class="o-layout__row">
      <div class="o-layout__col-m-6">
        <alert v-if="error" type="error" class="u-margin-bottom--15">{{error}}</alert>
        <account-form
          :account="account"
          :handleSubmit="handleSubmit"
          :onPropertyChanged="onPropertyChanged"
          ref="accountForm"
          >
          <div class="o-layout__row">
            <div class="o-layout__col-s-6">
              <nn-button variant="back" :to="{name: 'findGoldenRecord'}">Terug</nn-button>
            </div>
            <div class="o-layout__col-s-6">
              <nn-button variant="next"  type="submit" class="u-float-right">Volgende</nn-button>
            </div>
          </div>
        </account-form>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import AccountForm from './../accountForm'

export default {
  inject: ['loader'],
  components: {AccountForm},
  data: function () {
    return {
      account: {},
      error: null,
      errors: {}
    }
  },
  computed: {
    ...mapState([
      'businessPartner'
    ]),
  },
  mounted: function () {
    // check if business partner is still same as route param
    if (this.businessPartner && this.businessPartner.GoldenBPId === this.$route.params.goldenRecordId) {
      return;
    }
    // if not then validate the route param record
    this.loader.start();
    this.findBusinessPartner(this.$route.params.goldenRecordId)
      .finally(() => {
        this.loader.stop();
        if (!this.businessPartner) {
          // if not valid then reroute back to find golden record
          this.$router.push({ name: 'findGoldenRecord' });
        }
      })
  },
  methods: {
    ...mapActions([
      'createAccount',
      'findBusinessPartner'
    ]),
    onPropertyChanged(name, value) {
      this.account = {
        ...this.account,
        [name]: value
      };
    },

    handleSubmit() {
      this.error = null;
      this.loader.start();
      this.account.Customer_GR_ID = this.businessPartner.GoldenBPId;
      this.account.Admin = 'true';
      this.createAccount(this.account)
        .then(() => {
          this.$router.push({ name: 'confirmAccount', params: { username: this.account.Username } })
        })
        .catch(e => {
          if (e.errorList && e.errorList.find(el => el.code === "DUPLICATE USERNAME")) {
            if(this.$refs.accountForm){
              this.$refs.accountForm.forceInvalidResponseFromServer('Username', this.account.Username)
            }
          } else {
            this.error = "Er is iets mis gegaan bij het aanmaken van de gebruiker.";
          }
        })
        .finally(() => {
          this.loader.stop();
        });
    },
    resetErrors: function () {
      this.errors = {};
    }
  }
}
</script>