import {mapState} from 'vuex';

export default {
  template: '<nn-stepper :steps="steps" :activeStep="activeStep"></nn-stepper>',
  props: ['current'],
  computed: {
    ...mapState([
      'businessPartner'
    ]),
    steps() {
      return [
        {name: 'Zoeken', to: '/find-golden-record'},
        {name: 'Accountgegevens', to: this.businessPartner ? '/account-details/' + this.businessPartner.GoldenBPId : ''},
        {name: 'Bevestiging'},
      ]
    },
    activeStep() {
      return parseFloat(this.current)
    }
  }
}