import template from "./datatable.html";

export default {
  name: "Datatable",
  template,
  props: {
    rows: { type: Array, default: [] },
    displayedColumns: { type: Object, default: function () {
      return {}
    } },
    multiSelect: { type: Boolean, default: false },
    selection: { type: Array, default: function () {
      return []
    } },
    elementsPerPage: { type: Number, default: 10 },
    border: { type: Boolean, default: true },
    crud: { type: Boolean, default: false },
    displayHeaders: { type: Boolean, default: true },
    showPagination: { type: Boolean, default: true },
  },
  data() {
    return {
      currentPage: 1,
    };
  },
  methods: {
    displayColumn(row, col) {
      return (row[col] && row[col].length > 0) ? row[col] : '-';
    },
    isPager() {
      return (this.showPagination && this.numPages() > 1);
    },
    numPages() {
      return Math.ceil(this.rows.length / this.elementsPerPage);
    },
    getRows() {
      if (this.showPagination) {
        const start = (this.currentPage - 1) * this.elementsPerPage;
        const end = start + this.elementsPerPage;
        return this.rows.slice(start, end);
      }
      return this.rows
    },
    changePage(page) {
      this.currentPage = page;
    },
    clickRow(evt, index) {
      if (this.multiSelect) {
        this.updateSelection(index);
        evt.stopPropagation();
      }
    },
    clickCheckBox(evt) {
      evt.preventDefault();
      evt.stopPropagation();
      return false;
    },
    updateSelection(index) {
      const checkbox = document.querySelector('input[name="row_' + index + '"]');
      checkbox.checked = !checkbox.checked;

      const selection = this.rows.filter( (val, index) => (document.querySelector('input[name="row_' + index + '"]').checked));

      this.$emit('updateSelection', selection);
    },
    isChecked(index) {
      return this.selection.find((item) => this.deepCompare(this.rows[index], item));
    },
    isCrud(index) {
      return (index === (this.columns.length - 1) && this.crud);
    },
    hasRows() {
      return !!(this.rows.length);
    },
    deepCompare(obj1, obj2) {
      // Loop through properties in object 1
      for (let p in obj1) {
        //Check property exists on both objects
        if (obj1.hasOwnProperty(p) !== obj2.hasOwnProperty(p)) {
          return false;
        }

        switch (typeof (obj1[p])) {
        // Deep compare objects
        case 'object':
          if (!Object.compare(obj1[p], obj2[p])) {
            return false;
          }
          break;
        // Compare function code
        case 'function':
          if (typeof (obj2[p]) === 'undefined' || (p !== 'compare' && obj1[p].toString() !== obj2[p].toString())) {
            return false;
          }
          break;
        // Compare values
        default:
          if (obj1[p] !== obj2[p]) {
            return false;
          }
        }
      }

      // Check object 2 for any extra properties
      for (let q in obj2) {
        if (typeof (obj1[q]) === 'undefined') {
          return false;
        }
      }
      return true;
    }
  },
  computed: {
    columns() {
      if (this.rows.length < 1) {
        return [];
      }
      return (this.displayedColumns.length < 1) ? Object.keys(this.rows[0]) : Object.keys(this.displayedColumns);
    },
    columnsLabels() {
      return Object.keys(this.displayedColumns).map((key) => {
        return this.displayedColumns[key];
      });
    }
  },
};
