import Vue from "vue";
import VueRouter from 'vue-router'

import Home from '../pages/home.vue';

import FindGoldenRecord from "../pages/create-account/findGoldenRecord.vue";
import AccountDetails from "../pages/create-account/accountDetails.vue";
import ConfirmAccount from "../pages/create-account/confirmAccount.vue";

import EditAccount from '../pages/manage-account/editAccount.vue';

const createRouter = () => {
  if (!TESTING) {
    Vue.use(VueRouter);
    return new VueRouter({
      routes: [
        { path: '/', name: 'home', component: Home},

        // Account creation pages
        { path: '/find-golden-record', name: 'findGoldenRecord', component: FindGoldenRecord },
        { path: '/account-details/:goldenRecordId', name: 'accountDetails', component: AccountDetails },
        { path: '/confirm-account-create/:username', name: 'confirmAccount', component: ConfirmAccount },

        // Manage account
        { path: '/edit-account/:username', name: 'editAccount', component: EditAccount}
      ],
      scrollBehavior() {
        return { x: 0, y: 0 }
      }
    });
  }
}

export default createRouter;