export default {
  template: '<div></div>',
  data: function() {
    return {
      loadingOverlay: {}
    }
  },
  created: function() {
    this.loadingOverlay = new window.app.Loadingoverlay();
    this.loadingOverlay.show();
  },
  destroyed: function() {
    this.loadingOverlay.hide();
    this.loadingOverlay.destroy();
  }
}