<template>
  <form @submit.prevent="formSubmit" class="account-form" v-if="account" ref="accountForm" novalidate>
    <div class="o-layout">
      <div class="o-layout__row" v-if="editMode">
        <div class="o-layout__col-s-12 u-margin-bottom">
          <nn-input-field
          label="Golden record"
          v-model="account.Customer_GR_ID"
          required
          name="Customer_GR_ID"
          @input="onPropertyChanged('Customer_GR_ID', $event)"
          @blur="validateFormElement('Customer_GR_ID', $event.target.value)"
          @keydownEnter="validateFormElement('Customer_GR_ID', $event.target.value)"
          :invalid="!isValidatedAndValid('Customer_GR_ID')"
          :error="validationMessage('Customer_GR_ID')"
          ></nn-input-field>
        </div>
      </div>
      <div class="o-layout__row" v-if="editMode">
        <div class="o-layout__col-s-12">
          <nn-input-field
            class="u-margin-bottom"
            label="Gebruikersnaam"
            :value="account.Username"
            name="username"
            readonly
          />
        </div>
      </div>
      <div class="o-layout__row">
        <div class="o-layout__col-s-6">
          <nn-input-field
            label="Voornaam"
            class="u-margin-bottom"
            v-model="account.FirstName"
            name="firstname"
            required
            @input="onPropertyChanged('FirstName', $event)"
            @blur="validateFormElement('FirstName', $event.target.value)"
            @keydownEnter="validateFormElement('FirstName', $event.target.value)"
            :invalid="!isValidatedAndValid('FirstName')"
            :error="validationMessage('FirstName')"
          />
        </div>
        <div class="o-layout__col-s-6">
          <nn-input-field
            class="u-margin-bottom"
            label="Tussenvoegsel"
            v-model="account.FamilyNamePrefix"
          />
        </div>
      </div>
      <div class="o-layout__row">
        <div class="o-layout__col-s-12">
          <nn-input-field
            class="u-margin-bottom"
            label="Achternaam"
            v-model="account.FamilyName"
            name="familyname"
            required
            @input="onPropertyChanged('FamilyName', $event)"
            @blur="validateFormElement('FamilyName', $event.target.value)"
            @keydownEnter="validateFormElement('FamilyName', $event.target.value)"
            :invalid="!isValidatedAndValid('FamilyName')"
            :error="validationMessage('FamilyName')"
          />
        </div>
      </div>
      <div class="o-layout__row" v-if="!editMode">
        <div class="o-layout__col-s-12">
          <nn-input-field
            class="u-margin-bottom"
            label="Gebruikersnaam"
            v-model="account.Username"
            name="username"
            required
            pattern="^[a-zA-Z0-9-_.]+$"
            minLength= "2"
            maxLength= "50"
            @input="onPropertyChanged('Username', $event)"
            @blur="validateFormElement('Username', $event.target.value)"
            @keydownEnter="validateFormElement('Username', $event.target.value)"
            :invalid="!isValidatedAndValid('Username')"
            :error="validationMessage('Username')"
          />
        </div>
      </div>
      <div class="o-layout__row">
        <div class="o-layout__col-s-12">
          <nn-input-field
            class="u-margin-bottom"
            label="Telefoonnummer"
            v-model="account.Telephonenumber"
            name="phone"
            required
            @input="onPropertyChanged('Telephonenumber', $event)"
            @blur="validateFormElement('Telephonenumber', $event.target.value)"
            @keydownEnter="validateFormElement('Telephonenumber', $event.target.value)"
            :invalid="!isValidatedAndValid('Telephonenumber')"
            :error="validationMessage('Telephonenumber')"
            type="tel"
          />
        </div>
      </div>
      <div class="o-layout__row">
        <div class="o-layout__col-s-12">
          <nn-input-field
            class="u-margin-bottom"
            label="E-mailadres"
            v-model="account.EmailAddress"
            name="email"
            type="email"
            @input="onPropertyChanged('EmailAddress', $event)"
            @blur="validateFormElement('EmailAddress', $event.target.value)"
            @keydownEnter="validateFormElement('EmailAddress', $event.target.value)"
            :invalid="!isValidatedAndValid('EmailAddress')"
            :error="validationMessage('EmailAddress')"
            required
          />
        </div>
      </div>
      <slot></slot>
    </div>
  </form>
</template>

<script>
import ValidationMixin from '@nn/psdl-modules/src/app-vue-shared/validationMixin';
import validations from './validations';

export default {
  name: 'accountForm',
  mixins: [ ValidationMixin(validations) ],
  props: {
    onPropertyChanged: Function,
    handleSubmit: Function,
    account: Object,
    editMode: Boolean,
  },
  methods: {
    async formSubmit() {
      this.validateForm(this.account);
      if(this.isFormValid){
        this.handleSubmit();
      } else {
        await this.$nextTick();
        document.querySelector('form.account-form input.c-form-text--error').focus();
      }
    },
    forceInvalidResponseFromServer (elementName, value) {
      this.validateFormElement(elementName, value, true)
    }
  },
  computed:{
  },
  mounted (){
    if(!this.editMode){
      this.removeConfiguration('Customer_GR_ID')
    }
  }
}

</script>
