import template from './searchForm.html';
import Alert from './alert.js';

export default {
  template,
  components: { Alert },
  props: {
    label: {
      type: String
    },
    placeholder: {
      type: String
    },
    clearOnSubmit: {
      type: Boolean,
      default: false
    },
    alert: {
      type: String
    }
  },
  data () {
    return {
      searchInput: ''
    }
  },
  methods: {
    handleSubmit () {
      this.$emit('onSubmit', this.searchInput.trim());

      if(this.clearOnSubmit === true) {
        this.searchInput = '';
      }
    }
  }
}
