import Vue from "vue";
import Vuex from 'vuex';
import {asArray} from "@nn/psdl-utility/src/object-utils";


import {api, config} from '@nn/nn-api'
import {asString, fullName, organisationName} from "../utils";

config.setFetchMethod(fetch);
config.setFetchCredentials('same-origin');

Vue.use(Vuex);

const state = {
  account: null,
  otherAccounts: null,
  businessPartner: null,
  delegation: {},
  delegate: null,
  delegationModified: false
};

const mutations = {
  account(state, account) {
    if (!account) {
      state.account = null;
      return;
    }

    // Result returns more properties than we need and can conflict with the saving of the user.
    // Therefore we map it to an internal account model.

    // Empty props are returned as empty objects so we transform these to empty strings.

    // There's a mismatch in casing for EmailAddress when fetching data and when saving it.
    // We map it to the property casing used for saving account.
    state.account = {
      Admin: asString(account.Admin),
      Customer_GR_ID: asString(account.Customer_GR_ID),
      Username: asString(account.Username),
      FirstName: asString(account.FirstName),
      FamilyNamePrefix: asString(account.FamilyNamePrefix),
      FamilyName: asString(account.FamilyName),
      Telephonenumber: asString(account.Telephonenumber),
      EmailAddress: asString(account.Emailaddress || account.EmailAddress)
    };
  },
  otherAccounts(state, accounts) {
    state.otherAccounts = accounts;
  },
  businessPartner(state, businessPartner) {
    state.businessPartner = businessPartner;
  },
  delegation(state, delegation) {
    state.delegation = delegation;
    state.delegationModified = false;
  },
  delegate(state, delegate) {
    state.delegate = delegate;
  },
  removeDelegate(state, delegate) {
    const index = state.delegation.requested.indexOf(delegate);
    state.delegation.requested.splice(index, 1);
    state.delegationModified = true;
  },
  addDelegate(state) {
    state.delegation.requested.push(state.delegate);
    state.delegate = null;
    state.delegationModified = true;
  },
};

const actions = {
  findAccountByUsername({commit}, username) {
    return api.corporateAccess.searchBusinessUserForBusinessCustomer('Username', username).then(response => {
      // Usernames are unique, so result will either be empty or exactly 1
      commit('account', response.Results.BusinessUser);
    });
  },
  createAccount(context, account) {
    return api.corporateAccess.handleBusinessUserForBusinessCustomer(account);
  },
  updateAccount({commit}, account) {
    // map Action to new object to keep original clean, commit original if successful
    const updateAccount = Object.assign({}, account, {Action: "Update"});
    return api.corporateAccess.handleBusinessUserForBusinessCustomer(updateAccount).then(() => {
      commit('account', account);
    });
  },
  releaseAccount({state}) {
    const account = Object.assign({}, state.account, {AccountStatus: "RE", Action: "Update"});
    return api.corporateAccess.handleBusinessUserForBusinessCustomer(account)
  },
  resetPassword({state}) {
    return api.corporateAccess.resetPassword(state.account.Username);
  },

  findBusinessPartner({commit}, goldenRecordId) {
    return api.corporateAccess.findBusinessPartnerForCall(goldenRecordId).then(response => {
      commit('businessPartner', response.BusinessPartner)
    });
  },
  findDelegate({commit, state}, goldenRecordId) {
    return api.corporateAccess.findBusinessPartnerForCall(goldenRecordId).then(response => {
      if (response.BusinessPartner) {
        commit('delegate', {
          // Map to Internal Delegate structure
          OrganisationID: response.BusinessPartner.GoldenBPId,
          OrganisationName: organisationName(response.BusinessPartner),
          // BusinessRoleId -> will be fetched if added
          Identification: {
            OrganisationID: state.delegation.goldenRecordId,
            OrganisationType: 'BC'
          }
        });
      } else {
        commit('delegate', null);
      }
    });
  },
  findAccountsForDelegation({commit, dispatch, state}, searchText) {
    if (/^\d{10}$/.test(searchText)) {
      // Search by golden record ID
      return api.corporateAccess.searchBusinessUserForBusinessCustomer('Customer_GR_ID', searchText)
        .then(response => {
          let accounts = asArray(response.Results.BusinessUser);
          if (accounts) {
            commit('account', accounts.find(a => a.Admin === 'true'));
            commit('otherAccounts', accounts.filter(a => a.Username !== state.account.Username));
          } else {
            commit('account', null);
            commit('otherAccounts', null);
          }
        });
    }

    // search by username
    return dispatch('findAccountByUsername', searchText).then(() => {
      if (state.account) {
        // now we know the golden record ID and can search for all other related accounts
        return api.corporateAccess.searchBusinessUserForBusinessCustomer('Customer_GR_ID', state.account.Customer_GR_ID)
          .then(response => {
            let accounts = asArray(response.Results.BusinessUser);
            if (accounts) {
              commit('otherAccounts', accounts.filter(a => a.Username !== state.account.Username))
            } else {
              commit('otherAccounts', null)
            }
          })
      }
      commit('otherAccounts', null)

    });
  },
  prepareForDelegation({state, commit, dispatch}, username) {
    return dispatch('findAccountByUsername', username).then(() => {
      if (!state.account) {
        return Promise.reject();
      }

      const delegation = {
        goldenRecordId: state.account.Customer_GR_ID,
        username: state.account.Username,
        contactName: fullName(state.account),
        contactEmail: state.account.EmailAddress,
        original: [],
        requested: []
      };

      // get business partner
      const businessPartnerPromise = api.corporateAccess.findBusinessPartnerForCall(delegation.goldenRecordId)
        .then(response => {
          delegation.organisationName = organisationName(response.BusinessPartner);
        });

      // get current delegates
      const delegatePromise = api.corporateAccess.getBusinessCustomerAuthorisationForBusinessCustomer(delegation.goldenRecordId).then(result => {
        if (result.BusinessCustomerAuthorisation.Delegates) {
          const delegatePromises = [];
          const delegates = asArray(result.BusinessCustomerAuthorisation.Delegates);

          delegates.forEach(delegate => {
            delegatePromises.push(
              api.corporateAccess.findBusinessPartnerForCall(delegate.Identification.OrganisationID)
                .then(response => {
                  /*
                  * Internal Delegate structure matches structure used for adding and deleting delegates instead of structure received in this call.
                  * Mapping is done below.
                  *
                  * Internal Delegate:
                  * {
                  *   OrganisationID       -> ID of delegated organisation
                  *   OrganisationName     -> Name of delegated organisation
                  *   BusinessRoleId       -> Business Role ID for delegated organisation
                  *   Identification: {
                  *     OrganisationID     -> ID of base organisation
                  *     OrganisationType   -> Type of base organisation (always BC for now)
                  *   }
                  * }
                  * */
                  const internalDelegate = {
                    OrganisationID: delegate.Identification.OrganisationID,
                    OrganisationName: organisationName(response.BusinessPartner),
                    BusinessRoleId: delegate.BusinessRoleData.BusinessRoleId,
                    Identification: {
                      OrganisationID: delegation.goldenRecordId,
                      OrganisationType: 'BC'
                    }
                  };

                  // to start the original and requested lists are the same
                  // later used to determine what to add and what to delete
                  delegation.original.push(internalDelegate);
                  delegation.requested.push(internalDelegate);
                })
            );
          });

          return Promise.all(delegatePromises);
        }
      });

      return Promise.all([businessPartnerPromise, delegatePromise]).then(() => {
        commit('delegation', delegation);
      });
    })
  },
  addDelegateWithBusinessRole({state, commit}) {
    return api.corporateAccess.handleBusinessRoleDefinitionForBusinessCustomer(state.delegate.OrganisationID, 'Volledig beheer', true)
      .then(response => {
        state.delegate.BusinessRoleId = response.BusinessRoleId;
        commit('addDelegate');
      });
  },
  addDelegates(context, delegates) {
    const promises = [];
    const errors = [];

    delegates.forEach(delegate => {
      promises.push(
        api.corporateAccess.handleBusinessCustomerAuthorisationsForBusinessCustomer(delegate.OrganisationID, 'Add', delegate, delegate.BusinessRoleId)
          .catch(e => {
            // gather errors in an array (note this can't be done in Promise.all because it will fail on first error,
            // but we want all to finish and give a list of any errors.
            // This does mean that some companies will persist to server, only the ones giving an error will not have been added.
            errors.push({
              delegate,
              message: e.errorList[0].description
            });
          })
      )
    });

    return Promise.all(promises).then(() => {
      if (errors.length > 0) {
        // ensure error propagation
        return Promise.reject(errors);
      }
    });
  },
  deleteDelegates(context, delegates) {
    const promises = [];
    delegates.forEach(delegate => {
      promises.push(
        api.corporateAccess.handleBusinessCustomerAuthorisationsForBusinessCustomer(delegate.OrganisationID, 'Delete', delegate, delegate.BusinessRoleId)
      )
    });
    return Promise.all(promises);
  },
};

const store = new Vuex.Store({
  state,
  mutations,
  actions
});

export {store, state, actions, mutations}
