import {mapState} from 'vuex';

export default {
  template: '<nn-stepper :steps="steps" :activeStep="activeStep"></nn-stepper>',
  props: ['current'],
  computed: {
    ...mapState([
      'account',
      'delegation'
    ]),
    steps() {
      return [
        {name: 'Zoeken', to: '/find-account-for-delegates'},
        {name: 'Koppelen', to: this.account ? '/assign-delegates/' + this.account.Username : ''},
        {name: 'Controleren', to: this.account && this.delegation && this.delegation.username ? '/check-delegation/' + this.delegation.username : ''},
        {name: 'Bevestiging', to: '/confirm-delegation'},
      ]
    },
    activeStep() {
      return parseFloat(this.current)
    }
  }
}