const validResult = { valid: true, message: '' };

/**
 * Provides a mixin for validating your form using your own validation methods.
 * @param {object} configuration - A Validation configuration
 * @returns {object} Validation mixin
 * @example
 *   import getValidationMixin from '@nn/psdl-modules/src/app-vue-shared/validationMixin';
 *
 *   getValidationMixin ({
 *     firstName: (value) => ({ valid:!!value, message: 'This is a required field' }),
 *     lastName: (value) => ({ valid:!!value, message: 'This is a required field' }),
 *   })
 */
export default (configuration) => ({
  data () {
    return {
      validationResults: Object
        .keys(configuration)
        .reduce((prev, curr) => {
          prev[curr] = validResult;
          return prev;
        }, {})
    }
  },
  computed: {
    isFormValid () {
      return Object
        .keys(this.validationResults)
        .map(k => this.validationResults[k].valid)
        .every(r => Boolean(r));
    },
  },
  methods: {
    isValidatedAndValid (name) {
      const validationResult = this.validationResults[name];
      return validationResult ? validationResult.valid : true;
    },
    validationMessage (name) {
      const validationResult = this.validationResults[name];
      return validationResult ? validationResult.message : '';
    },
    addConfiguration (extraConfig){
      if (!extraConfig || Array.isArray(extraConfig) || typeof extraConfig !== 'object') {
        return false
      }
      configuration = {...configuration, ...extraConfig};

      this.validationResults = {
        ...this.validationResults,
        ...Object
          .keys(extraConfig)
          .reduce((prev, curr) => {
            prev[curr] = validResult;
            return prev;
          }, {})
      }

      return configuration;
    },
    removeConfiguration (configName){
      if (Object.keys(configuration).includes(configName)) {
        delete configuration[configName];
        delete this.validationResults[configName]
        return configuration;
      }

      return 'Non-existing key'
    },
    validateFormElement (name, value, forcedInvalid) {
      const validator = configuration[name];
      if (!validator) {
        this.validationResults[name] = validResult;
      }
      let validationResult = validator(value, forcedInvalid);
      if (!validationResult) {
        validationResult = validResult;
      }
      this.validationResults = {
        ...this.validationResults,
        [name]: validationResult,
      };
    },
    validateForm (data) {
      Object.keys(configuration)
        .forEach(name => {
          this.validateFormElement(name, data[name]);
        });
    }
  }
})
