<template>
<div>
  <h1>Admin account Mijn NN Zakelijk aanmaken</h1>
  <account-steps current="0"></account-steps>
  <div class="o-layout u-margin-top--30">
    <div class="o-layout__row">
      <div class="o-layout__col-s-6">
        <search-form placeholder="Zoek op golden record" @onSubmit="handleSubmit" :alert="message"></search-form>
      </div>
    </div>
    <div v-if="businessPartner" class="u-margin-top--30">
      <div class="o-layout__row">
        <div class="o-layout__col-s-12">
          <b>Accountgegevens</b>
        </div>
      </div>
      <div class="o-layout__row">
        <div class="o-layout__col-s-4">Golden record</div>
        <div class="o-layout__col-s-4">{{businessPartner.GoldenBPId}}</div>
      </div>
      <div class="o-layout__row">
        <div class="o-layout__col-s-4">Bedrijf</div>
        <div class="o-layout__col-s-4">{{businessPartner.Organisation ? businessPartner.Organisation.OrganisationName : ''}}</div>
      </div>
      <div class="o-layout__row u-margin-top--15">
        <div class="o-layout__col-s-4"><nn-button variant="back" :to="{name: 'home'}">Terug</nn-button></div>
        <div class="o-layout__col-s-4"><nn-button variant="next" :to="{name: 'accountDetails', params: {goldenRecordId: businessPartner.GoldenBPId}}">Volgende</nn-button></div>
      </div>
    </div>
    <div v-else class="o-layout__row u-margin-top--30">
      <div class="o-layout__col-s-6">
        <nn-button variant="back" :to="{name: 'home'}">Terug</nn-button>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import {mapActions, mapState} from 'vuex'

export default {
  inject: ['loader'],
  data: function () {
    return {
      message: ''
    }
  },
  mounted: function () {
    this.$store.commit('businessPartner', null);
  },
  computed: {
    ...mapState([
      'businessPartner'
    ])
  },
  methods: {
    ...mapActions([
      'findBusinessPartner'
    ]),
    handleSubmit: function (searchText) {
      this.loader.start();
      this.message = '';
      this.findBusinessPartner(searchText)
        .then(() => {
          if (!this.businessPartner) {
            this.message = 'Bedrijf niet gevonden';
          }
        })
        .finally(() => {
          this.loader.stop();
        })
    },
  }
}
</script>
