export default {
  bind (el) {
    el = (el.nodeName !== 'INPUT')
      ? (el.querySelector('input') || el)
      : el;

    el.addEventListener('keyup', () => {
      el.value = el.value.replace(/[^0-9.]/g, '');
      if (el.value.length > 15) {
        el.value = el.value.substr(0, 15);
      }
    })
  },

  unbind (el) {
    el.removeEventListener('keyup');
  }
};
