<template>
<div>
  <h1>Account controleren</h1>
  <nn-modal ref="changeConfirmation">
    <h1>{{modal.header}}</h1>
    <hr/>
    <p>{{modal.message}}</p>
    <div class="u-float-right">
      <nn-button @click="closeModal">Sluiten</nn-button>
    </div>
    <br>
    <br>
  </nn-modal>
  <div class="o-layout">
    <div class="o-layout__row">
      <div class="o-layout__col-m-9">
        <div class="o-layout">
          <div class="o-layout__row">
            <div class="o-layout__col-m-9">
              <alert v-if="status === 'failed'" type="error" class="u-margin-bottom">
                Er is iets misgegaan bij het ophalen van de gebruiker.
              </alert>
              <loading-overlay v-if="status === 'action'"> Please wait...</loading-overlay>
              <account-form
                v-if="status === 'done' || status === 'action'"
                :account="accountData"
                :handleSubmit="handleSubmit"
                :onPropertyChanged="onPropertyChanged"
                :editMode="editMode"
              >
                <div class="o-layout__row">
                  <div class="o-layout__col-s-12 u-margin-bottom">
                    <nn-button variant="back" :to="{ name: 'home' }">Terug</nn-button>
                    <nn-button type="submit" class="u-float-right">Opslaan</nn-button>
                  </div>
                </div>
              </account-form>
            </div>
          </div>
        </div>
      </div>
      <div v-if="status === 'done'" class="o-layout__col-s-3">
        <h2 class="h2--secondary">Uw acties</h2>
        <ul class="c-list">
          <li class="c-list__item"><nn-link @click.prevent="sendReleaseAccount">Vrijgeven</nn-link></li>
          <li class="c-list__item"><nn-link @click.prevent="sendPasswordReset">Wachtwoord reset</nn-link></li>
        </ul>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import AccountForm from './../accountForm'

export default {
  props: {
    config: Object
  },
  components: {AccountForm},
  data () {
    return {
      status: 'action',
      modal: {},
      validationErrors: {},
      editMode: true,
      accountData: {},
    }
  },
  computed: {
    ...mapState([ 'account' ]),
  },
  async mounted () {
    try {
      await this.findAccountByUsername(this.$route.params.username);
      this.accountData = {...this.account};
      this.status = 'done';
    } catch (err) {
      this.status = 'failed';
    }
  },
  methods: {
    ...mapActions([
      'findAccountByUsername',
      'updateAccount',
      'releaseAccount',
      'resetPassword'
    ]),
    openModal (header, message) {
      this.modal = { header, message };
      this.$refs.changeConfirmation.open();
    },
    onPropertyChanged(name, value) {
      this.accountData = {
        ...this.accountData,
        [name]: value
      };
    },
    closeModal () {
      this.$refs.changeConfirmation.close();
    },
    async handleSubmit () {
      try {
        this.status = 'action';
        await this.updateAccount(this.accountData);
        this.openModal('Opslaan gegevens', 'Gegevens zijn opgeslagen');
        this.status = 'done';
      } catch (err) {
        this.status = 'failed';
      }
    },
    async sendReleaseAccount () {
      try {
        this.status = 'action';
        await this.releaseAccount();
        this.openModal( 'Vrijgeven account', 'Het account van de gebruiker is vrijgegeven. De gebruiker ontvangt hierover een e-mail.');
        this.status = 'done';
      } catch (err) {
        this.status = 'failed';
      }
    },
    async sendPasswordReset () {
      try {
        this.status = 'action';
        await this.resetPassword();
        this.openModal( 'Wachtwoord reset', 'Het wachtwoord van de gebruiker is gereset. De gebruiker ontvangt een e-mail.');
        this.status = 'done';
      } catch (err) {
        this.status = 'failed';
      }
    }
  }
}
</script>