import template from "./alert.html";

export default {
  name: "alert",
  template,
  props: {
    type: { type: String, default: '' },
    isOpen: {
      type: Boolean,
      default: true
    },
    showCloseButton: {
      type: Boolean,
      default: false
    },
  },
  computed: {
    alertClasses() {
      return 'c-notification c-notification--' + this.type;
    },
  },
  methods: {
    closeAlert() {
      this.$emit('closeAlert', this);
    },
  },
}