import template from "./textInput.html";

export default {
  name: "TextInput",
  template,
  model: {
    prop: "value",
    event: "change"
  },
  props: {
    type: { type: String, default: "text" },
    name: { type: String, default: "" },
    label: { type: String, default: "" },
    value: { type: String, default: "" },
    placeholder: { type: String, default: "" },
    disabled: { type: Boolean, default: false },
    readonly: { type: Boolean, default: false },
    error: { type: Boolean, default: false },
    errorMessage: { type: String, default: "" },
    className: { type: String },
    maxLength: { type: Number, default: 255 },
  },
  methods: {
    blur(e) {
      this.$emit('blur', e);
    },
    focus(e) {
      this.$emit('focus', e);
    },
    updateValue(e) {
      this.$emit('input', e);
    }
  },
  computed: {
    getClass() {
      let classes = (this.error) ? 'c-form-text c-form-text--error' : 'c-form-text';
      classes = (this.className) ? classes + ' ' + this.className : classes;
      return classes;
    },
    getIconClass() {
      if (this.error) {
        return 'c-icon c-icon--pict-warning u-color--medium-orange u-font-size-m';
      }
      return 'c-icon c-icon--check u-color--medium-orange u-font-size-m u-invisible';

    },
  }
};
